import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'gatsby'

const Header = (props) => (
    <header id="header" className="alt">
        {/* <Link to="/" className="logo"><strong>ShouldITip.in</strong> <span>by HTML5 UP</span></Link> */}
        <Link to="/" className="logo"><strong>ShouldITip.in</strong> </Link>
        <nav>
            <a className="menu-link" onClick={props.onToggleMenu} href={null}>MENU</a>
        </nav>
    </header>
)

Header.propTypes = {
    onToggleMenu: PropTypes.func
}

export default Header
